.prices__swiper {
    height: 260px;

    .swiper-slide:nth-child(6) {
        text-transform: uppercase;
        font-size: 35px;
        display: flex;
        align-items: center;
        justify-content: center;
        background-color: rgb(39, 39, 39);
        color: aliceblue;
    }

    .swiper-horizontal>.swiper-pagination-bullets,
    .swiper-pagination-bullets.swiper-pagination-horizontal {
        bottom: 16px;
    }
}